<!--
 * @Description: 文章详情页
 -->
 <template>
  <div id="content—container">
    <!--左边长列表-->
    <div class="box1-left">
      <currentLocate></currentLocate>
      <div class="box2-left" id="talent-apply">
        <ul class="about">
          <li class="first">发布时间：<span>{{item.createTime}}</span></li>
          <li>截止时间：<span></span></li>
          <li style="width:23s%;border-right:none;">已浏览 <span>次</span></li>
        </ul>
        <ul class="itme">
          <li class="first"><span>{{item.position}}</span></li>
          <li class="job-compony"><span>{{item.companyName}}</span></li>
          <li class="job-width">公司规模：<span>{{item.companySize}}</span></li>
          <li class="job-class">公司性质：<span>{{item.companyNature}}</span></li>
          <li class="job-type">公司行业：<span>{{item.industry}}</span></li>
        </ul>
        <ul class="pay">
          <li>
            <div>薪资待遇：<span style="color:#fd5f39;">{{item.salary}}</span></div>
          </li>
          <li>
            <div>招聘职位：<span>{{item.positionName}}</span></div>
          </li>
          <li>
            <div>学历要求：<span>{{item.degreeRequired}}</span></div>
          </li>
          <li>
            <div>招聘人数：<span>{{item.hiring}}</span></div>
          </li>
          <li>工作年龄：<span>{{item.workAge}}</span></li>
          <li>联系电话<span>{{item.phone}}</span></li>
          <div class="supply">立即申请</div>
          <span></span>
        </ul>
        <ul class="job-title">
          <li :class="{active2:index==currentTab}" v-for="(tab,index) in tabs" :key="tab.name" @mouseover="currentTab=index">
            <a>{{tab.name}}</a>
          </li>
        </ul>
        <div class="job-content" style="width:100%;">
          <div v-html="tabs[currentTab].content"></div>
        </div>
      </div>
    </div>
    <!--右边长列表-->
    <div class="box4-right">
      <div class="box6-right">
        <user-box></user-box>
      </div>
      <div class="box7-right">
        <keep-alive>
          <pb-list :widgetMessage="tabs2[0]" :widgetType="1"></pb-list>
        </keep-alive>
      </div>
      <div class="box12-right">
        <ul class="title">
          <li class="list_title">
            <a style="margin-left:15px;" class="title-active">广告招收栏目</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <slide2></slide2>
          </keep-alive>
        </div>
      </div>
    </div>
  </div>
</template>
 <script>
import UserBox from "@/components/common/tools/user-box";
import slide2 from "@/components/common/tools/slide2";
import currentLocate from "@/components/common/tools/currentLocate";
import PbList from "@/components/common/tools/pb-list";
import { proofreading, getMainContent } from "@/api/getData";

export default {
  name: "contentContainer",
  data() {
    return {
      query: JSON.parse(this.$route.query.queryMsg), //利用JSON传输，避免刷新后出现[Object,Object]的情况
      item: {
        areas: "",
        companyIntroduction: "",
        companyName: "",
        companyNature: "",
        companySize: "",
        createTime: "",
        degreeRequired: "",
        hiring: "",
        id: 181,
        industry: "",
        jobArea: "",
        jobDescription: "",
        phone: "",
        position: "",
        positionName: "",
        salary: "",
        viewCount: "",
        workAge: ""
      },
      tabs: [
        {
          name: "职位描述",
          content: ""
        },
        {
          name: "公司介绍",
          content: ""
        },
        {
          name: "公司其他职位",
          content: ""
        }
      ],
      currentTab: 0
    };
  },
  computed: {
    tabs2() {
      let value = global.tabs;
      let value1, value2;
      value1 = value.filter(item => {
        if (item.belong == "活动") return true;
      });
      if (!value[0].message && !value[0].message && !value[0].message) {
        proofreading.call(this); //数据未加载，调用函数校对加载
      }
      value = [value1];
      return value;
    }
  },
  methods: {
    getData() {
      let that = this;
      let url = that.HOST + "/position/getPositionInfo";
      let id = JSON.parse(that.$route.query.queryMsg).id;
      getMainContent(url, id)
        .then(res => {
          that.item = res.message;
          that.item.createTime = that.item.createTime.slice(0, 10);
          that.tabs[0].content = that.item.jobDescription;
        })
        .catch(err => {
          that.$message.error({
            showClose: true,
            message: "获取招聘信息异常",
            duration: 2000
          });
        });
    },
    init() {
      //显示路径
      let index = this.$router.options.routes[0].children.findIndex(
        menu => menu.showName == "招聘"
      );
      global.currentPath.data = [index, 1];
      //获取页面信息
      this.getData();
    },
    tabChange() {}
  },
  activated() {
    this.init();
  },
  components: {
    UserBox,
    slide2,
    PbList,
    currentLocate
  }
};
</script>
<style lang="scss" scoped>
$blue: #127de7;
#content—container /deep/ img {
  max-width: 650px;
}
@mixin adv {
  margin: 5px auto;
  width: 1200px;
  height: 125px;
  overflow: hidden;
}

@mixin i-box {
  box-shadow: 0 0 1px #bebebe;
  height: 100%;
  overflow: hidden;
  border-radius: 2px;
}
@mixin border {
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid rgba(204, 204, 204, 0.377);
}
#content—container {
  @include adv;
  height: 100%;
}
/*右边小框重复样式*/
@mixin litter_one_tab_box {
  @include i-box;
  @include border;
  height: 312px;
  width: 390px;
  float: right;
  margin-bottom: 10px;
}
@mixin left {
  margin: 5px auto;
  width: 795px;
  float: left;
  box-sizing: border-box;
  overflow: hidden;
}
@mixin right {
  margin: 5px auto;
  width: 390px;
  float: right;
  box-sizing: border-box;
  overflow: hidden;
}
.box1-left {
  margin-top: 20px;
  width: 775px;
  float: left;
  @include i-box;
  padding: 13px;
  background: rgba(255, 255, 255, 0.95);
}
.box2-left {
  @include i-box;
  padding: 13px;
  background: rgba(255, 255, 255, 0.596);
  margin: 10px 0;
}

.box4-right {
  margin-top: 20px;
  width: 390px;
  //@include i-box;
  float: right;
}
.box4-right div {
  margin-right: 0.5px;
}
.box12-right,
.box6-right,
.box7-right {
  @include litter_one_tab_box;
  background: rgba(255, 255, 255, 0.95);
}
@mixin class1 {
  width: 100%;
  overflow: hidden;
  box-sizing: border-box;
  margin: 5px 0;
  padding: 10px 0;
  border-bottom: #ccc 3px solid;
}

.active2 {
  & {
    box-sizing: border-box;
    color: #535353;
    font-weight: 600;
  }
  &::after {
    content: "";
    position: absolute;
    bottom: -3px;
    left: 0;
    width: 100%;
    height: 4px;
    border-bottom: $blue 3px solid;
    z-index: 99;
  }
}
#talent-apply {
  .about {
    & {
      @include class1;
    }
    .first {
      margin-left: -15px;
    }
    li {
      text-align: center;
      display: inline-block;
      box-sizing: border-box;
      padding-right: 15px;
      width: 30%;
      border-right: #ada2a2 2px solid;
      line-height: 15px;
    }
  }
  .itme {
    & {
      @include class1;
      padding: 5px 0 10px 0;
    }
    .first {
      font-weight: 500;
      display: block;
      font-size: 30px;
      color: #333;
      margin: 0 0 8px;
    }
    .job-compony {
      display: block;
    }
    li {
      text-align: left;
      padding: 3px 5px;
      display: inline-block;
      box-sizing: border-box;
      padding-right: 35px;
    }
  }
  .pay {
    & {
      @include class1;
      border-bottom: none;
    }
    li {
      & {
        text-align: left;
        padding: 5px 8px;
        box-sizing: border-box;
        padding-right: 90px;
      }
      span {
        padding: 10px;
      }
    }
    .supply {
      display: inline-block;
      color: #fff;
      background: $blue;
      font-weight: 500;
      font-size: 17px;
      padding: 8px 11px;
      margin: 10px 0;
      border-radius: 5px;
      cursor: pointer;
    }
  }
  .job-title {
    & {
      @include class1;
      overflow: initial;
      padding: 0;
    }
    li {
      & {
        font-size: 17px;
        display: inline-block;
        text-align: left;
        padding-bottom: 10px;
        font-weight: 500;
        margin-right: 20px;
        color: #404040;
        cursor: pointer;
        user-select: none;
        position: relative;
      }
      a {
        text-align: left;
      }
    }
  }
  .job-content {
    @include i-box;
    min-height: 200px;
    margin-top: 20px;
    padding: 0 10px;
  }
  .box4 {
    @include right;
    @include i-box;
  }
  .title {
    margin-bottom: 12px;
    margin-top: 2px;
    width: 100%;
    height: 28px;
    line-height: 28px;
  }
  .title li {
    float: left;
  }
  .list_title a {
    color: #7b7b7b;
    cursor: default;
    font-size: 1em;
    font-weight: 500;
    line-height: 25px;
    vertical-align: middle;
    margin-right: 10px;
  }
  .list_title a:hover {
    color: $blue;
    border-bottom: 2px solid $blue;
    cursor: default;
    padding-bottom: 4px;
  }
}
</style>



